<template>
  <div class="seePage">
    <!-- 采购入库列表查看 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>{{title}}</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">采购入库</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看采购入库单</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="采购组织：" size="mini">
          {{instoragList.company_name}}
        </el-form-item>
        <el-form-item label="单据号：" size="mini">
          {{instoragList.instorage_sn}}
        </el-form-item>
        <el-form-item label="来源单据:" size="mini">
          {{instoragList.order_sn}}
        </el-form-item>
        <el-form-item label="业务日期：" size="mini">
          {{instoragList.business_time}}
        </el-form-item>
        <el-form-item label="入库类型：" size="mini">
          {{instoragList.type}}
        </el-form-item>
        <el-form-item label="仓库名称：" size="mini">
          {{instoragList.warehouse_name}}
        </el-form-item>
        <el-form-item label="供应商(厂家)：" size="mini">
          {{instoragList.supplier_name}}
        </el-form-item>
        <el-form-item label="部门：" size="mini">
          {{instoragList.department_name}}
        </el-form-item>
        <el-form-item label="采购员：" size="mini">
          {{instoragList.buyer_name}}
        </el-form-item>
        <el-form-item label="付款方式：" size="mini">
          {{instoragList.receivables_mode_name}}
        </el-form-item>
        <el-form-item label="分摊方式：" size="mini">
          {{instoragList.other_price_type_name}}
        </el-form-item>
        <el-form-item label="其他费用：" size="mini" v-if="!(params.notFieldAuthHeader && params.notFieldAuthHeader.includes('other_price'))">
          {{instoragList.other_price}}
        </el-form-item>
        <el-form-item label="厂家发货单号：" size="mini">
          {{instoragList.factorysend_sn}}
        </el-form-item>
        <el-form-item label="厂家发货日期：" size="mini">
          {{instoragList.factorysend_time}}
        </el-form-item>
        <el-form-item label="审核状态：" size="mini">
          {{instoragList.auditor_status_name}}
        </el-form-item>
        <el-form-item label="交货方式" size="mini">
          {{ instoragList.transaction_type_name }}
        </el-form-item>
        <el-form-item label="入库来源" size="mini">
          {{ instoragList.instorage_source_name }}
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <xTable :ajax='{ url: "purchaseInfoInStorage", params: {instorage_id:$route.query.instorage_id, is_info_caller: 1},  result:"data.data.goodsList" }' fieldUrl='purchase/InStorage/infoInStorage' ref='xTable' :sign="indexSign" :isPage="false" :footerMethod='footerMethod' :tableColumn='tableColumn' @getData='getTableData' />
      <div>
        <el-form :inline="true" :model="instoragList" class="demo-form-inline" ref="paramsFooter">
          <el-form-item label="单据备注：" size="mini" style="width:900px">
            <span>{{instoragList.remark}}</span>
          </el-form-item>
          <br />
          <el-form-item label="创建人：" size="mini">
            <el-input v-model="instoragList.add_user_name" maxlength="100" disabled></el-input>
          </el-form-item>
          <el-form-item label="创建时间：" size="mini">
            <el-input v-model="instoragList.add_time" maxlength="100" disabled></el-input>
          </el-form-item>
          <el-form-item label="修改人：" size="mini">
            <el-input v-model="instoragList.update_user_name" maxlength="100" disabled></el-input>
          </el-form-item>
          <el-form-item label="修改时间：" size="mini">
            <el-input v-model="instoragList.update_time" maxlength="100" disabled></el-input>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <div slot="footer" class="dialog-footer">
      <!--未审核-->
      <template v-if="$route.query.after_auditor_print_status == 1">
        <el-button @click="printSheet" type="primary" v-if="authority.b_print==1 && TableData.instoragList.auditor_status==2">打印</el-button>
      </template>
      <template v-else>
        <el-button @click="printSheet" type="primary" v-if="authority.b_printinginstorage==1">打印</el-button>
      </template>
      <template v-if="instoragList.auditor_status==1">
        <el-button type="primary" @click="shenHe" v-if="authority.b_pass==1">审 核</el-button>
        <el-button @click="abolishFun" v-if="authority.b_invalid==1">作 废</el-button>
      </template>
      <template v-if="instoragList.auditor_status==2">
        <el-button @click="AUDIT_GET" v-if="authority.b_nopass==1">弃审</el-button>
      </template>
      <!--作废-->
      <template v-if="instoragList.auditor_status==2 || instoragList.auditor_status==3 || (authority.b_invalid!=1 && authority.b_pass!=1)">
        <el-button @click="goBack">返 回</el-button>
      </template>
      <el-button @click="goBack" v-else>取 消</el-button>
    </div>
    <errorDialog :errorData="trunErrTable" :errorState="errorState" :titleName='[
						{ field: "serial", title: " 行号/单据号",width: 200 },
						{ field: "msg", title: "失败原因"}]'></errorDialog>
    <!-- <div v-if="printingStatusGet">

    </div> -->
    <printInstorage ref="printInstorage" title="purchaseReceipt" fieldUrl='purchase/InStorage/printInStorage'></printInstorage>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import printInstorage from "common/components/public/PublicPrint.vue"; //打印采购单
import { TableHead } from "./config/instorageSee.js";
import errorDialog from "common/components/plugin/eldialog/errorDialog.vue";
export default {
  name: "purchaseInstorageSee",
  mixins: [TableHead],
  data() {
    return {
      goodsList: [],
      authority: {},
      loading: false,
      indexSign: {},
      trunErrTable: {},
      errorState: false,
      instoragList: {},
      chaid: false,
      hasAuditorStatusPrint: null
    };
  },
  props: {
    title: {
      type: String,
      default: "采购管理"
    },
    goBackUrl: {
      type: String,
      default: "/purchase/instorage"
    }
  },
  components: {
    printInstorage,
    errorDialog
  },
  computed: {
    ...mapGetters(["printingStatusGet"])
  },
  methods: {
    ...mapActions(["printingStatus"]),
    goBack() {
      this.$router.push({ path: this.goBackUrl });
    },
    getTableData(val) {
      this.goodsList = val.data.data.goodsList
      this.instoragList = val.data.data.instoragList;
      this.params = val.data.data;
      this.goodsList = val.data.data.goodsList
    },
    AUDIT_GET() {
      //弃审
      this.$baseData
        .checkCarDoctor({
          bill_type: 1,
          bill_id: this.$route.query.instorage_id,
          operation_type: 5
        })
        .then(() => {
          this.$axMethod
            .unAuditorPass({
              instorage_id: this.$route.query.instorage_id,
              is_list: 2
            })
            .then(res => {
              if (res.data.code == 200) {
                let errData = res.data.data; //报错
                if (errData.length) {
                  let checkArry = [];
                  errData.forEach(item => {
                    checkArry.push(Number(item.serial));
                  });
                  let activeColor = this.goodsList.filter(
                    val => checkArry.includes(val.serial_number) == true
                  );
                  this.indexSign = {
                    arr: activeColor,
                    str: "serial_number"
                  };
                  this.trunErrTable = {
                    data: errData,
                    msg: res.data.msg
                  };
                  this.errorState = true;
                } else {
                  let msg = res.data.msg ? res.data.msg : errData;
                  this.$message({
                    message: msg,
                    type: "success"
                  });
                  this.goBack();
                }
              }
            });
        });
    },
    printSheet() {
      this.$refs.printInstorage.printPdf({
        instorage_id: this.$route.query.instorage_id
      });
    },
    shenHe() {
      //审核
      this.$baseData
        .checkCarDoctor({
          bill_type: 1,
          bill_id: this.$route.query.instorage_id,
          operation_type: 4
        })
        .then(() => {
          this.$axios({
            method: "post",
            url: this.$api.purchaseAuditorPassInStorage,
            data: {
              instorage_id: this.$route.query.instorage_id
            }
          }).then(res => {
            if (res.data.code == 200) {
              this.$message({
                message: res.data.msg,
                type: "success"
              });
              this.goBack();
            }
          });
        });
    },
    abolishFun() {
      //作废
      this.$baseData
        .checkCarDoctor({
          bill_type: 1,
          bill_id: this.$route.query.instorage_id,
          operation_type: 6
        })
        .then(() => {
          this.$axios({
            method: "post",
            url: this.$api.purchaseAuditorInvalidInStorage,
            data: {
              instorage_id: this.$route.query.instorage_id
            }
          }).then(res => {
            if (res.data.code == 200) {
              this.$message({
                message: res.data.data,
                type: "success"
              });
              this.goBack();
            }
          });
        });
    }
  },
  created () {
    console.log(this)
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.hasAuditorStatusPrint = window.localStorage.hasAuditorStatusPrint;
  }
};
</script>
<style lang="less" scoped>
.bzstyle {
  position: relative;
  top: 10px;
}
.bzstyle span {
  position: relative;
  width: 3%;
  font-size: 12px;
  top: 2px;
}
.bzstyle .el-input {
  position: absolute;
  width: 97%;
  left: 3%;
  top: 0;
}
</style>
